import React, { useState, useEffect } from "react";

export default function SearchBar(props) {
  const [text, setText] = useState("");

  useEffect(() => {
    props.setSearchText(text);
  }, [text]);

  return (
    <div className="searchBar">
      <input
        id="searchInput"
        type="text"
        placeholder="Search"
        value={props.value}
        onChange={(event) => setText(event.target.value)}
      />
    </div>
  );
}
